/**** ÏΩòÌÖêÏ∏† + ÏΩúÎ†âÏÖò Î∞∞Ïó¥ Ìï©Ï≥êÏÑú */

// 1. ÏµúÏã†Ïàú Ï†ïÎ†¨(createdAt)
// - ÏΩòÌÖêÏ∏† Î∞è ÏΩúÎ†âÏÖòÏùÑ Ïö∞Î¶¨ ÏõπÏÇ¨Ïù¥Ìä∏Ïóê Í∞ÄÏû• ÏµúÍ∑ºÏóê Ï†ÄÏû•Ìïú ÏàúÏÑúÎåÄÎ°ú ÎÖ∏Ï∂ú
function sortLatestArr(contentsData, collectionData) {
  console.log("ÏµúÏã†Ïàú Ï†ïÎ†¨");

  // 1) ÏΩòÌÖêÏ∏† Î∞∞Ïó¥Í≥º ÏΩúÎ†âÏÖò Î∞∞Ïó¥ÏùÑ Ìï©ÏπúÎã§.(spread operator: Ï†ÑÍ∞úÏó∞ÏÇ∞Ïûê)
  const newArr = [...contentsData, ...collectionData];
  // 2) Î¨∏ÏûêÏó¥ - ÎÇ†ÏßúÎ•º ÎÇ†Ïßú - ÎÇ†ÏßúÎ°ú Î≥ÄÍ≤Ω
  const sortedArr = newArr.sort(
    (a, b) => new Date(b.createdAt) - new Date(a.createdAt)
  );
  return sortedArr;
}

// 2. Ï¶êÍ≤®Ï∞æÍ∏∞ Ïàú
// - Ï¶êÍ≤®Ï∞æÍ∏∞Î•º ÏßÄÏ†ïÌïú ÎÇ†ÏßúÏàú
// *Ï¶êÍ≤®Ï∞æÍ∏∞Í∞Ä ÏßÄÏ†ïÎêú ÏΩòÌÖêÏ∏† Î∞è ÏΩúÎ†âÏÖòÏùÄ ÏàòÏ†ïÎêú ÎÇ†Ïßú ÏàúÏÑúÎåÄÎ°ú ÎÖ∏Ï∂ú
// (Ïòà: 22.8.23 23:34 Ï†ÄÏû• + 22.9.10Ïóê Ï¶êÍ≤®Ï∞æÍ∏∞ ÏßÄÏ†ï > 22.9.6 14:00 Ï†ÄÏû• + 22.9.6 14:03Ïóê Ï¶êÍ≤®Ï∞æÍ∏∞ ÏßÄÏ†ï)
function sortFavoritesArr(contentsData, collectionData) {
  console.log("Ï¶êÍ≤®Ï∞æÍ∏∞Ïàú Ï†ïÎ†¨");
  const newArr = [...contentsData, ...collectionData];

  // Ï¶êÍ≤®Ï∞æÍ∏∞ ÏûàÎäî Í≤ÉÎßå Î∂ÑÎ•ò
  const favoritesArr = newArr.filter((data) => data.favorite == true);
  // Ï¶êÍ≤®Ï∞æÍ∏∞ ÏóÜÎäî Í≤ÉÎßå Î∂ÑÎ•ò
  const NonFavoritesArr = newArr.filter((data) => data.favorite == false);

  // Ï¶êÍ≤®Ï∞æÍ∏∞ Îêú Í≤ÉÎì§ÏùÑ ÏµúÏã†ÏàúÏúºÎ°ú Ï†ïÎ†¨
  const sortedFavorites = favoritesArr.sort(
    (a, b) => new Date(b.updatedAt) - new Date(a.updatedAt)
  );
  // Ï¶êÍ≤®Ï∞æÍ∏∞ ÎêòÏßÄ ÏïäÏùÄ Í≤ÉÎì§ÏùÑ ÏµúÏã†ÏàúÏúºÎ°ú Ï†ïÎ†¨
  const sortedNonFavorites = NonFavoritesArr.sort(
    (a, b) => new Date(b.createdAt) - new Date(a.createdAt)
  );

  // ÏúÑ Îëê Î∞∞Ïó¥ÏùÑ Ìï©Ïπ®
  const sortedArr = [...sortedFavorites, ...sortedNonFavorites];

  return sortedArr;
}

// 3. ÏùΩÏùÑ Í∏∞Ìïú Ïàú(
function sortDeadlineArr(contentsData, collectionData) {
  // const newArr = [...contentsData, ...collectionData];
  console.log("ÏùΩÏùÑÍ∏∞ÌïúÏàú Ï†ïÎ†¨");

  // Í∏∞ÌïúÏù¥ ÏûàÎäî Í≤ÉÎßå Î∂ÑÎ•ò
  const deadlineContentArr = contentsData.filter((data) => data.deadline);
  // Í∏∞ÌïúÏù¥ ÏóÜÎäî Í≤ÉÎßå Î∂ÑÎ•ò
  const nonDeadlineContentArr = contentsData.filter((data) => !data.deadline);

  const nonDeadLineAllArr = [...nonDeadlineContentArr, ...collectionData];

  // Í∏∞ÌïúÏù¥ ÏûàÎäî Í≤ÉÎì§ÏùÑ ÏùΩÏùÑ Í∏∞ÌïúÏù¥ ÏûÑÎ∞ïÌïú ÏàúÏúºÎ°ú Ï†ïÎ†¨
  const sortedDeadline = deadlineContentArr.sort(
    (a, b) => new Date(a.deadline) - new Date(b.deadline)
  );
  // Í∏∞ÌïúÏù¥ ÏóÜÎäî Í≤ÉÎì§ÏùÑ ÏµúÏã†ÏàúÏúºÎ°ú Ï†ïÎ†¨
  const sortedNonDeadline = nonDeadLineAllArr.sort(
    (a, b) => new Date(b.createdAt) - new Date(a.createdAt)
  );

  // ÏúÑ Îëê Î∞∞Ïó¥ÏùÑ Ìï©Ïπ®
  const sortedArr = [...sortedDeadline, ...sortedNonDeadline];

  return sortedArr;
}

/**** ÏΩòÌÖêÏ∏† / ÏΩúÎ†âÏÖò Î≥ÑÍ∞úÎ°ú */

// 1. ÏµúÏã†Ïàú Ï†ïÎ†¨(createdAt) - Î∞∞Ïó¥ Ìïú Í∞ú ÎåÄÏÉÅ
function sortLatest(data) {
  const sortedArr = data.sort(
    (a, b) => new Date(b.createdAt) - new Date(a.createdAt)
  );
  return sortedArr;
}

// 2. Ï¶êÍ≤®Ï∞æÍ∏∞ Ïàú - Ï¶êÍ≤®Ï∞æÍ∏∞Î•º ÏßÄÏ†ïÌïú ÎÇ†Ïßú Ïàú
function sortFavorites(data) {
  const arr = data.sort(
    (a, b) => new Date(b.createdAt) - new Date(a.createdAt)
  );
  const sortedArr = arr.filter((content) => content.favorites == true);
  return sortedArr;
}

// 3. ÏùΩÏùÑ Í∏∞Ìïú Ïàú(ÏùΩÏùÑ Í∏∞Ìïú ÏßßÏùÄ Ïàú?)
function sortDeadline(data) {
  const arr = data.filter((content) => content.deadline);

  const sortedArr = arr.sort(
    (a, b) => new Date(b.deadline) - new Date(a.deadline)
  );
  return sortedArr;
}

export {
  sortLatestArr,
  sortFavoritesArr,
  sortDeadlineArr,
  sortLatest,
  sortFavorites,
  sortDeadline,
};
